.container{

    cursor: pointer;
    position: relative;
}

.title{

    position: absolute;
    margin-top: -40px;
    height: 40px;
    width: 300px;
    padding-left: 20px;
    padding-top: 8px;
    background-color: rgba(0, 0, 0, 0.8);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.img:focus, .img:hover{
    transform: scale(1.1);
    z-index: 1;
}

.img{

    border-radius: 10px;
    transition: transform 200ms;
}